.js-backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--color-backdrop);
  opacity: 1;
  visibility: visible;
  transition: visibility var(--transition-params), opacity var(--transition-params);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.js-backdrop--hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.js-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lds-spinner {
  width: 80px;
  height: 80px;
  color: #f5f5f5;
  display: inline-block;
  position: relative;
}

.lds-spinner div {
  transform-origin: 40px 40px;
  animation: lds-spinner 1.2s linear infinite;
}

.lds-spinner div:after {
  content: " ";
  width: 6px;
  height: 18px;
  background: #fff;
  border-radius: 20%;
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
}

@keyframes lds-spinner {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

